.gridcharge__whatgridcharge {
    display: flex;
    flex-direction: column;

    padding: 2rem;
    background: var(--color-footer);

    background: -moz-radial-gradient(circle at 30% -100%, #021204 25%, rgba(2, 24, 8, 1) 85%, rgba(8, 48, 16, 1) 100%);
    background: -webkit-radial-gradient(circle at 30% -100%, #021204 25%, rgba(2, 24, 8, 1) 85%, rgba(8, 48, 16, 1) 100%);
    background: -o-radial-gradient(circle at 30% -100%, #021204 25%, rgba(2, 24, 8, 1) 85%, rgba(8, 48, 16, 1) 100%);
    background: -ms-radial-gradient(circle at 30% -100%, #021204 25%, rgba(2, 24, 8, 1) 85%, rgba(8, 48, 16, 1) 100%);
    background: radial-gradient(circle at 30% -100%, #021204 25%, rgba(2, 24, 8, 1) 85%, rgba(8, 48, 16, 1) 100%);

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}


.gridcharge__whatgridcharge-feature {
    display: flex;
}

.gridcharge__whatgridcharge-feature .gridcharge__features-container__feature {
    margin: 0;
}
.gridcharge__whatgridcharge-feature .gridcharge__features-container_feature-text {
    max-width: 700px;
}

.gridcharge__whatgridcharge-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    margin: 4rem 0 2rem;
}

.gridcharge__whatgridcharge-heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
    max-width: 510px;
}

.gridcharge__whatgridcharge-heading p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
    cursor: pointer;
}

.gridcharge__whatgridcharge-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;

    margin-top: 2rem;
}

.gridcharge__whatgridcharge-container .gridcharge__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

.gridcharge__whatgridcharge-container .gridcharge__features-container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .gridcharge__whatgridcharge-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gridcharge__whatgridcharge-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {

    .gridcharge__whatgridcharge-feature .gridcharge__features-container__feature {
        flex-direction: column;
    }

    .gridcharge__whatgridcharge-feature .gridcharge__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .gridcharge__whatgridcharge-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {

    .gridcharge__whatgridcharge-container .gridcharge__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}